<template>
    <button @click="handleClick" class="el-button" :disabled="disabled" :type="nativeType" :class="[
        type? 'el-button--' + type:'',
        buttonSize ? 'el-button--' + buttonSize: '',
        {
            'is-disabled': disabled
        }
        ]">
        <!-- slot 表button中的文字 -->
        <span v-if="$slots.default"><slot></slot></span>
    </button>
</template>

<script>
export default {
    props:{
        type:{
            type: String,
            default: 'default'
        },
        size: String,
        disabled: Boolean,
        nativeType:{ 
            type:String,
            default: 'button'
        }
    },
    computed:{
        buttonSize(){
            return this.size;
        }
    },
    methods:{
        handleClick(evt){
            this.$emit('click',evt);
        }
    }
}
</script>

<style>

</style>



