<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <el-badge class="item" :value="1" :max="9">
      <el-button nativeType="submit" @click="addComment" size="medium">评论</el-button>
    </el-badge>
    <el-badge class="item" is-dot>  
      <el-button type="primary" :disabled="saveDisabled" @click="doSave" size="small">回复</el-button>
    </el-badge> 
    <el-button type="primary">前往</el-button>  
    <el-badge class="item" :value="10" :max="9">  
      <el-button type="primary"  icon="el-icon-edit" size="mini">点我</el-button> 
    </el-badge>   
    
    <!-- <HelloWorld/> -->
  </div>
</template>

<script>
import ElBadge from '@/components/ElBadge.vue'
import ElButton from '@/components/ElButton.vue'
import HelloWorld from './components/HelloWorld'

export default {
  data(){
    return {
      saveDisabled: true,
    }
  },
  mounted(){
    setTimeout(() => {
      this.saveDisabled = false;
    }, 5000);
  },
  name: 'app',
  components: {
    HelloWorld,
    ElButton,
    ElBadge
  },
  methods:{
    addComment(){
      console.log('添加评论')
    },
    doSave(){
      console.log('回复')
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



